body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navigation {
  position: relative;
  margin-top: 15px;
  width: 100%;
  height: 4rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0 2rem;
  font-family: "Pathway Extreme", sans-serif;
}

.nav-bar {
  display: flex;
  padding: 10px;
  justify-content: right;
}

#nav-dropdown {
  color: white;
  font-size: 20px;
  font-weight: bold;
  margin-top: 40px;
}

.nav-bar li {
  font-size: 20px;
  padding: 40px;
  display: inline;
  list-style: none;
  text-align: center;
  font-weight: bolder;
}

.nav-bar li:hover {
  text-transform: capitalize;
  
}

.nav-bar li:last-child {
  margin-left: -25px;
}

@media (max-width: 768px) {
  .navigation {
    flex-wrap: wrap;
    height: auto;
  }

  .nav-bar {
    flex-direction: row;
    align-items: stretch;
  }

  .nav-bar li {
    padding: 10px;
    font-size: 15px;
     
  }

  #nav-dropdown {
    margin-top: 17px;
    padding-top: 0.5px;
    font-size: 15px;
  }
}

@media (max-width: 480px) {
  .navigation {
    padding: 0 1rem;
  }
}





